<template>
	<div class="wrapper">
		<div class="content">
			<div class="w-left">
				<ctitle :title="'区域来访信访量排行'" class="w-l-title"></ctitle>
				<div class="echarts">
					<horizontal-bar :params="xfbmBarParams"></horizontal-bar>
				</div>
			</div>
			<div class="w-right">
				<div class="r-top">
					<ctitle :title="'来访信访量四级登记机构分布'"></ctitle>
					<div class="ct-content">
						<div class="count">
							<div class="title">全省来访信访量</div>
							<allCount class="all-count" :count="allcount"></allCount>
							<div class="detail">
								<detailList :params="detailList"></detailList>
							</div>
						</div>
						<div class="echart">
							<loopPie class="chart" :params="rsgmzbPieParams"></loopPie>
						</div>
        	</div>
				</div>
				<div class="r-bottom">
					<ctitle :title="'来访信访量四级登记机构走势'"></ctitle>
          <div class="inner">
						<!-- <div class="inner-left">
							<div class="zs-title" style="">月度同比环比</div>
							<div class="zs-detail">
								<List :params="ydtbhbParams"></List>
							</div>
						</div> -->
						<div class="inner-right">
							<timeChange class="timeChange" @change="zsTimeClick"></timeChange>
            	<LinesR :params="xflzsLineParams"></LinesR>
						</div>
          </div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import {get_http} from "@/api/index";
import horizontalBar from "@/components/bar/HorizontalBar";
import ctitle from "@/components/title/index";
import allCount from "@/components/list/allCount";
import detailList from "@/components/list/detailList";
import loopPie from "@/components/pie/LoopPie1";
import ring from "@/components/pie/ring";
import LinesR from "@/components/line/LinesR";
import timeChange from "@/components/timeChange/index";
import incNumber from "@/components/incNumber/index";
import List from '../components/list.vue'
	export default {
		name: 'wx',
		components: {
			horizontalBar,
			ctitle,
			allCount,
			detailList,
			loopPie,
			ring,
			LinesR,
			timeChange,
			incNumber,
			List
		},
		data() {
			return {
				ydtbhbParams: {
					data: [],
					map: {}
				},
				// 来访信访量四级登记机构分布
				rsgmzbPieParams: {
					canvasStyle: {
						width: "100%",
						height: "100%"
					},
					data: [],
					options: {
						borderColor: '#050e27',
						borderWidth: 13,
						colors: this.$STANDARD.gradientColors,
						radius: ["40%", "55%"],
						center: ["46%", "50%"],
						centerRadius: ["42%", "43%"],
						labelRich: {
							b: this.fontSize(0.2),
							d: this.fontSize(0.22),
							bHeight: this.fontSize(0.3)
						},
						centerMsg: '件次',
						length: 20,
						length2: 45,
						borderWidth: this.fontSize(0.05),
						bRichPadding: [30, -45, 0, -45],
						tooltip: {
							show: true
						},
						labelShow: true,
						itemLabelShow: true, //数据扇区label是否显示
						//图例
						legend: {
							show: false
						},
						rich: {
							bfontsize: "14%" //文字大小
						},
						labelSize: "22%", // label字号
						rollFlag: true
					}
				},
				detailList: {},
				// 信访形式占比
        allcount: -1,
				// 1.区域来访信访量排行
				xfbmBarParams: {
					data: [],
					canvasStyle: {
						width: "100%",
						height: "100%"
					}
				},
				// 来访信访量初件重件联名件走势
				xflzsLineParams: {
					data: [],
					colors: [
						// 紫
						{
							line: "rgba(14,149,210, 1)",
							start: "rgba(14,149,210, 0.5)",
							end: "rgba(14,149,210, 0.5)"
						},
						{
							line: "rgba(19,240,255, 1)",
							start: "rgba(19,240,255, 0.5)",
							end: "rgba(19,240,255, 0.5)"
						},
						{
							line: "rgba(78,237,153, 1)",
							start: "rgba(78,237,153, 0.5)",
							end: "rgba(78,237,153, 0.5)"
						},
						{
							line: "rgba(255,200,51, 1)",
							start: "rgba(255,200,51, 0.5)",
							end: "rgba(255,200,51, 0.5)"
						}
					],
					option: {
						lineWidth: '0',
						legendWidth: 10,
						legendHeight: 10,
						YaxisLineShow: true,
						splitNumber: 4,
						splitLineShow: false,
						legendShow: true,
						legenditemWidth: 22,
						legenditemHeight: 4,
						axisLabelShow: true,
						axisTickShow: false,
						axisCategoryName: "月",
						// axisYName: "件次",
						axisLabelSize: 14,
						scales: true,
						axisLabelFontSize: 16,
						yaxisLabelFontSize: 14,
						Tofixed: false,
						smooth: false,
						areaShow: 1,
						smoothMonotone: "none",
						dataZoomshow: false,
						legendIcon: "rect",
						left: "5%",
						right: "10%",
						top: "15%",
						bottom: "10%",
						legendTop: "2%",
						legendRight: "10%"
					},
					canvasStyle: {
						width: "100%",
						height: "100%"
					},
					map: {
					},
					xName: "name"
				},
			}
		},
		mounted() {
			this.getAPI(this.date)
		},
		methods: {
			// 来访信访量四级登记机构走势
			getXflzsLineAPI(data, time) {
				let timetype = "",
					_year = "";
				if (time === "年") {
					timetype = "cy";
					_year = "[now-4, now]";
				} else {
					timetype = "cm";
					_year = data;
				}
				get_http("xftstjfxXfxsLfDjjgzs", {
					day: `[${this.date.startDate},${this.date.endDate}]`,
				})
					.then(res => {
						this.xflzsLineParams.option.axisCategoryName =
							time === "年" ? "年" : "月";
						this.xflzsLineParams.map = res.data.map;
						this.xflzsLineParams.data = res.data.data;
						// this.zsLineParams.option.axisCategoryName =
            // time === "年度" ? "年" : "月";
          	// this.zsLineParams.map = res.data.map;
          	// this.zsLineParams.data = res.data.data;
					})
					.catch(err => {
						console.log(err);
					});
			},
			getAPI(date) {
				// 1.区域来访信访量排行（左侧柱图）
        get_http("xftstjfxXfxsLfXfllb", {
					day: `[${date.startDate},${date.endDate}]`
				})
				.then(res => {
					this.xfbmBarParams.data = res.data.data.sort((a, b) => {return a.value - b.value;});
				})
				.catch(err => {
					console.log(err);
				});
				// 2.来访信访量四级登记机构分布(总数)
        get_http("xftstjfxXfxsLfLmfbTotal", {
          day: `[${date.startDate},${date.endDate}]`
        })
				.then(res => {
					this.allcount = res.data.data[0].value;
				})
				.catch(err => {
					console.log(err);
				});
				// 3.来访信访量四级登记机构分布(列表)
        get_http("xftstjfxXfxsLfLmfbList", {
          day: `[${date.startDate},${date.endDate}]`
        })
				.then(res => {
					this.detailList = res.data;
				})
				.catch(err => {
					console.log(err);
				});
				// 4.来访信访量四级登记机构分布(圆环)
				get_http("xftstjfxXfxsLfDjjgfbPie", {
					day: `[${date.startDate},${date.endDate}]`,
				})
				.then(res => {
					this.rsgmzbPieParams.data = res.data.data;
				})
				.catch(err => {
					console.log(err);
				});
				// 5.来访信访量四级登记机构走势--月度同比环比
				get_http("xftstjfxXfxsLfDjjgzsList", {
					day: `[${date.startDate},${date.endDate}]`,
				})
				.then(res => {
					this.ydtbhbParams = res.data;
					// this.ydtbhbParams.map = res.data.map;
					// this.ydtbhbParams.map = res.data.map;
				})
				.catch(err => {
					console.log(err);
				});
				this.getXflzsLineAPI(date, '年')
			},
			fontSize(res) {
				let docEl = document.documentElement,
					clientWidth =
						window.innerWidth ||
						document.documentElement.clientWidth ||
						document.body.clientWidth;
				if (!clientWidth) return;
				let fontSize = 100 * (clientWidth / 1920);
				return res * fontSize;
			},
			// 时间切换
			zsTimeClick(time) {
        this.getXflzsLineAPI(this.date, time);
      },
		},
		computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val, oval) {
        if (oval.startDate || oval.endDate) {
          this.getAPI(val);
        }
      },
    }
	}
</script>

<style lang="stylus" scoped>
.wrapper
	position relative
	top 0.8rem
	height calc(100% - 0.8rem)
	.content
		width 100%
		height 100%
		display flex
		padding .2rem .3rem
		box-sizing border-box
		display flex
		.w-left
			width 4.5rem
			height 100%
			position relative
			display flex
			flex-direction column
			.echarts
				// height calc(100% - 0.28rem)
				flex 1
				box-sizing border-box
				border-right 1px #91e3f7 dashed
		.w-right
			flex 1
			display flex
			flex-direction column
			.r-top
				flex 1.3
				height 100%
				.ct-content
					display flex
					align-items center
					height calc(100% - 0.28rem)
					.echart
						height 100%
						flex 1.5
					.count
						flex 2
						position relative
						.title
							font-size 18px
							color #fff
							text-align center
							// position absolute
							// top -38px
							// left 79px
							color #fff
							margin-bottom 15px
							margin-left -60px
						.detail
							margin-top 0.22rem
							>>>.inner
								ul
									display flex
									flex-wrap wrap
									justify-content center
									.item
										width 35%
			.r-bottom
				flex 1
				display flex
				flex-direction column
				position relative
				.inner
					flex 1
					display flex
					.inner-left
						flex 1
						display flex
						flex-direction column
						.zs-title
							height 0.55rem
							line-height 0.8rem
							font-size 18px
							color #00b0ff
							font-weight 600
							padding-left 19%
						.zs-detail
							width 100%
							flex 1
							display flex
							align-items center
							justify-content center
					.inner-right
						flex 2
						.timeChange
							position absolute
							right 85px
							top 5px
</style>