<template>
<div
    class="detail-list"
    v-loading="!listData.length"
    :element-loading-background="$STANDARD.loadingBgColor"
  >
    <ul>
			<li v-for="(item, index) in listData" :key="index">
				<p class="title">{{item.name || item.title}}</p>
				<div class="detail">
						<div class="d-title">同比</div>
						<span class="per-unit">+</span>
						<incNumber class="num" :incNumber="(item[mapKey[getArrayIndex('同比')]]*100).toFixed(2)"></incNumber>
						<span class="per-unit">%</span>
				</div>
				<div class="detail">
						<div class="d-title">环比</div>
						<span class="per-unit">+</span>
						<incNumber class="num" :incNumber="(item[mapKey[getArrayIndex('环比')]]*100).toFixed(2)"></incNumber>
						<span class="per-unit">%</span>
				</div>
			</li>
    </ul>
</div>
</template>

<script>
import incNumber from "@/components/incNumber/index";
export default {
	props: {
		params: {
			type: [Object, Array],
      default: null
		}
	},
	data() {
		return {
			mapKey: [],
			mapValue: [],
			numMaxLength: 0,
			listData: [],
		}
	},
	components: {
		incNumber
	},
	mounted() {
		// this.setLength();
	},
	methods: {
		setLength() {
			this.mapKey = Object.keys(this.params.map); // ['value_g0_1', 'value_g1_1']
			this.mapValue = Object.values(this.params.map); // ['同比', '环比']

			let numMaxArr = [];
			if (this.params instanceof Array) {
				this.listData = this.params;
			} else {
				this.listData = this.params.data;
			}

		},
		// 获取某个值在数组中的下标
		getArrayIndex(obj) {
			let i = this.mapValue.length;
			while (i--) {
				if (this.mapValue[i] === obj) {
					return i;
				}
			}
			return -1;
		}
	},
	watch: {
		params() {
			this.setLength();
		}
	}
}
</script>

<style lang="stylus" scoped>
ul
	li
		display flex
		height 0.6rem
		line-height 0.6rem
		justify-content center
		.title
			font-size 16px
			color #fff
		.detail
			display flex
			margin-left 35px
			.d-title
				color #00b0ff
				font-size 17px
				margin-right 15px
			.num
				font-size 17px
				color #ffde00
			.per-unit
				font-size 17px
				color #ffde00
</style>